@font-face {
  font-family: 'Gen Jyuu Gothic X';
  font-style: normal;
  font-weight: 400;
  src: url(fonts/GenJyuuGothicX-Medium.ttf) format('truetype');
}

@font-face {
  font-family: 'Gen Jyuu Gothic X';
  font-style: normal;
  font-weight: 700;
  src: url(fonts/GenJyuuGothicX-Bold.ttf) format('truetype');
}

.App {
  width: 1080px;
  height: 1920px;
  position: relative;
  font-family: 'Gen Jyuu Gothic X';
}

img {
  pointer-events: none;
}

* {
  -webkit-user-select: none; /* Safari 3.1+ */
  -moz-user-select: none; /* Firefox 2+ */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Standard syntax */
}

*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* general */

.action-btn {  
  width: 360px;
  height: 180px;
  position: relative;
  margin: 0 auto;
  border: 8px solid #AC5508;
  border-radius: 84px;
  color: #FFF;
  font-size: 60px;
  font-weight: 900;
  line-height: 180px;
  letter-spacing: .05em;
  text-align: center;
}

.action-btn:before {
  content: '';
  width: 360px;
  height: 180px;
  border-radius: 80px;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
  -webkit-transform: scaleX(.93) scaleY(.88);
      -ms-transform: scaleX(.93) scaleY(.88);
          transform: scaleX(.93) scaleY(.88);
  -webkit-transform-origin: left bottom;
      -ms-transform-origin: left bottom;
          transform-origin: left bottom;
  background-color: #EF8519;
}

.svg {
  position: absolute;
}

