.svg-intro-dialog { width: 130px; left: 104px; bottom: 272px; }

/* quiz page */

@-webkit-keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.question-wrapper {
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 180px auto 60px;
}

.question-count {
  font-size: 108px;
  font-weight: 900;
  color: #7EBC25;
  width: 200px;
}

.question-content {
  font-size: 48px;
  color: #1A1A1A;
  margin-left: 48px;
  padding-top: 12px;
}

.answer-options {
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 70%;
  height: 200px;
  margin: 0 auto;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.answer-option {
  text-align: center;
  position: absolute;
  display: table;
  left: 0;
  top: 0;
  width: 212px;
  height: 212px;
  color: #488A1F;
  border: 6px solid #3E7C18;
  border-radius: 50%;
  font-size: 36px;
}

.answer-option:nth-child(2) {
  left: 278px;
}

.answer-option:nth-child(3) {
  left: 556px;
}

.answer-option p {
  display: table-cell;
  vertical-align: middle;
  word-break: keep-all;
}

.false .answer-option.correct:after {
  content: '';
  background: url(./images/quiz/correct.svg);
  width: 60px;
  height: 60px;
  position: absolute;
  right: -8px;
  top: -8px;
  -webkit-animation: check .25s ease-in;
          animation: check .25s ease-in;
}

.answer-option.incorrect:after {
  content: '';
  background: url(./images/quiz/incorrect.svg);
  width: 60px;
  height: 60px;
  position: absolute;
  right: -8px;
  top: -8px;
  -webkit-animation: check .25s ease-in;
          animation: check .25s ease-in;
}

@-webkit-keyframes check {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes check {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.answer-option:before {
  content: '';
  width: 212px;
  height: 212px;
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
  -webkit-transform-origin: left bottom;
      -ms-transform-origin: left bottom;
          transform-origin: left bottom;
  display: block;
  background-color: #CDDC4C;
  border-radius: 50%;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.illustration {
  margin: 60px auto;
  position: relative;
}

.illustration:before {
  content: '';
  width: 264px;
  height: 180px;
  position: absolute;
  right: 144px;
  top: 60px;
}

.illustration.success:before {
  background: url(./images/quiz/success.svg) center center no-repeat;
  -webkit-animation: stamp .5s ease-in;
          animation: stamp .5s ease-in;
}

.illustration.failure:before {
  background: url(./images/quiz/failure.svg) center center no-repeat;
  -webkit-animation: stamp .5s ease-in;
          animation: stamp .5s ease-in;
}

@-webkit-keyframes stamp {
  0% {
    -webkit-transform: scale(5);
            transform: scale(5);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes stamp {
  0% {
    -webkit-transform: scale(5);
            transform: scale(5);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

.answer-description {
  position: absolute;
  left: 28%;
  width: 70%;
  font-size: 36px;
  color: #488A1F;
  margin-left: 48px;
  visibility: hidden;
  opacity: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

.answer-description::before {
  content: '說明';
  text-align: center;
  font-size: 28px;
  display: block;
  width: 56px;
  margin-bottom: 20px;
  border-bottom: 4px #488A1F dotted;
}

.answer-options.true .answer-option{
  visibility: hidden;
}

.answer-options.true .answer-option.correct{
  visibility: visible;
}

.quiz .action-btn {
  -webkit-animation: show .5s ease-in;
          animation: show .5s ease-in;
}

/* result page */

.result {
  /* background: radial-gradient(circle at center, #FFFFFF 0%, #FFFFFF 70%, #CDDC4C 70%, #CDDC4C 100%); */
  background-color: #CDDC4C;
  width: 100%;
  height: 100%;
}

.result-focus {
  background-color: #FFFFFF;
  width: 100vw;
  height: 100vw;
  border-radius: 50vw;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%) scale(2.4);
      -ms-transform: translateY(-50%) scale(2.4);
          transform: translateY(-50%) scale(2.4);
}

.result-title {
  position: absolute;
  top: 700px;
  width: 100%;
  font-size: 60px;
  color: #1A1A1A;
  text-align: center;
}

.result-score {
  font-size: 96px;
  font-weight: 900;
  color: #7EBC25;
  display: inline-block;
  width: 108px;
  text-align: center;
}

.result-details {
  position: absolute;
  top: 840px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 54px;
  margin: 48px auto;
}

.svg-result-each {
  width: 156px;
}

.detail-per-question {
  position: relative;
  opacity: .25;
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}

.detail-per-question:after {
  content: '';
  width: 48px;
  height: 48px;
  position: absolute;
  right: -4px;
  top: -4px;
}

.detail-per-question.correct {
  opacity: 1;
}

.detail-per-question.incorrect {
  opacity: 1;
}

.detail-per-question.correct:after {
  background: url(./images/quiz/correct.svg);
  -webkit-animation: check .25s ease-in;
          animation: check .25s ease-in;
}

.detail-per-question.incorrect:before {
  content: '';
  position: absolute;
  width: 148px;
  height: 148px;
  left: 4px;
  top: 4px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, .25);
}

.detail-per-question.incorrect:after {
  background: url(./images/quiz/incorrect.svg);
  -webkit-animation: check .25s ease-in;
          animation: check .25s ease-in;
}

.result-illustration {
  position: absolute;
  top: 640px;
  width: 100%;
  height: 600px;
  opacity: 0;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}

.result-description {
  position: absolute;
  left: 15%;
  top: 1320px;
  width: 70%;
  opacity: 0;
  word-break: keep-all;
  text-align: center;
  font-size: 42px;
  color: #1A1A1A;
  margin: 0 auto;
}

.result-btn {
  position: absolute;
  top: 1450px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0;
}

